<template>
  <view class="goods-item">
    <!-- ÂõæÁâá -->
    <view class="goods-item-image" @click="gotoDetail(goods)">
      <image :src="goods.goods_big_logo" class="goods-pic" mode="aspectFill"></image>
    </view>

    <!-- ÊñáÊú¨‰ø°ÊÅØ -->
    <view class="goods-item-info">
      <view class="goods-name" @click="gotoDetail(goods)">{{ goods.goods_name }}</view>
      
      <!-- ‰ª∑Ê†ºÂíåÊ†°Âå∫‰ø°ÊÅØË°å -->
      <view class="goods-footer" @click="gotoDetail(goods)">
        <view class="goods-price" v-if="goods && goods.goods_price">
          <text class="price-symbol">¬•</text>
          <text class="price-num">{{ goods.goods_price | tofixed }}</text>
        </view>
        
        <view class="goods-location">
          <text class="location-text">{{ goods.location || '‰ΩçÁΩÆÊú™Áü•' }}Ê†°Âå∫</text>
        </view>
      </view>

      <!-- üî• ‰øÆÊîπÔºöÂèëÂ∏ÉËÄÖ‰ø°ÊÅØ - Â∑¶ËæπÂ§¥ÂÉè+ÊòµÁß∞ÔºåÂè≥ËæπÊó∂Èó¥ -->
      <view class="publisher-info" @click="gotoProfile(goods.publisher_id)">
        <view class="publisher-left">
          <image 
            :src="publisherAvatar" 
            class="publisher-avatar"
            mode="aspectFill"
          ></image>
          <text class="publisher-name">{{ publisherName }}</text>
        </view>
        <text class="card-time">{{ formatTime(goods.add_time) }}</text>
      </view>
    </view>
  </view>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name:"my-goods",
  props: {
    goods: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
      isNavigating: false,
    };
  },
  computed: {
    ...mapState('m_user', ['openid']),
    
    publisherAvatar() {
      return this.goods?.publisher_avatarUrl || '/images/1.jpg';
    },
    publisherName() {
      return this.goods?.publisher_nickname || 'ÂåøÂêçÁî®Êà∑';
    },
    hoursSince() {
      let currentTimestamp = Math.floor(Date.now() / 1000);
      let diff = currentTimestamp - this.goods?.add_time;
      return Math.floor(diff / 3600);
    },
    isRecent() {
      return this.hoursSince !== null && this.hoursSince <= 72;
    },
    publishedText() {
      if (!this.isRecent) return "";
      const h = Math.max(1, this.hoursSince);
      return `${h}Â∞èÊó∂ÂÜÖÂèëÂ∏É`;
    },
  },
  methods: {
    async gotoProfile(openid) {
      if (!this.openid) {
        uni.switchTab({
          url: '/pages/my/my'
        })
        return
      }
      
      if (this.isNavigating) {
        console.log('‚è∏Ô∏è Ê≠£Âú®Ë∑≥ËΩ¨‰∏≠ÔºåËØ∑ÂãøÈáçÂ§çÁÇπÂáª');
        return;
      }
      
      this.isNavigating = true;
      
      try {
        const queryObj = { code: openid };
        const { data: res } = await uni.$http.get('/users/userinfo', queryObj);
        
        if (res.meta.status === 200) {
          console.log('Ë∑≥ËΩ¨Âà∞Áî®Êà∑‰∏ªÈ°µ');
          const publisher_info = res.message;
          const payload = encodeURIComponent(JSON.stringify(publisher_info));
          
          await uni.navigateTo({
            url: `/subpkg/profile/profile?publisher=${payload}`
          });
        }
      } catch (error) {
        console.error('‚ùå Ë∑≥ËΩ¨Áî®Êà∑‰∏ªÈ°µÂ§±Ë¥•:', error);
      } finally {
        setTimeout(() => {
          this.isNavigating = false;
        }, 500);
      }
    },
    
    formatTime(timestamp) {
      if (!timestamp) return 'ÂàöÂàö'
      
      const time = typeof timestamp === 'string' 
        ? new Date(timestamp).getTime() 
        : timestamp * 1000
      
      const now = Date.now()
      const diff = now - time
      
      const minute = 60 * 1000
      const hour = 60 * minute
      const day = 24 * hour
      
      if (diff < 0) return 'ÂàöÂàö'
      if (diff < minute) return 'ÂàöÂàö'
      if (diff < hour) return Math.floor(diff / minute) + 'ÂàÜÈíüÂâç'
      if (diff < day) return Math.floor(diff / hour) + 'Â∞èÊó∂Ââç'
      if (diff < 7 * day) return Math.floor(diff / day) + 'Â§©Ââç'
      
      const date = new Date(time)
      return `${date.getMonth() + 1}-${date.getDate()}`
    },
    
    async gotoDetail(item) {
      if (this.isNavigating) {
        console.log('‚è∏Ô∏è Ê≠£Âú®Ë∑≥ËΩ¨‰∏≠ÔºåËØ∑ÂãøÈáçÂ§çÁÇπÂáª');
        return;
      }
      
      this.isNavigating = true;
      
      try {
        console.log('üìä Â¢ûÂä†ÂïÜÂìÅÊµèËßàÊ¨°Êï∞:', item.goods_id);
        
        uni.$http.post('/goods/view', {
          goods_id: item.goods_id,
          user_openid: this.openid || null
        }).then(({ data: res }) => {
          if (res.meta.status === 200) {
            console.log('‚úÖ ÊµèËßàÊ¨°Êï∞Â¢ûÂä†ÊàêÂäü');
          }
        }).catch(error => {
          console.error('‚ùå ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Â§±Ë¥•:', error);
        });

        await uni.navigateTo({
          url: '/subpkg/goods_detail/goods_detail?goods_id=' + item.goods_id
        });
        
        console.log('‚úÖ Ë∑≥ËΩ¨ÊàêÂäü');
        
      } catch (error) {
        console.error('‚ùå Ë∑≥ËΩ¨Â§±Ë¥•:', error);
        this.isNavigating = false;
      } finally {
        setTimeout(() => {
          this.isNavigating = false;
        }, 500);
      }
    },
  },
  filters: {
    tofixed(num) {
      return Number(num).toFixed(2)
    }
  }
}
</script>

<style lang="scss">
.goods-item {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 12rpx;
  overflow: hidden;
  margin-bottom: 20rpx;
  box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.06);
  height: 550rpx;
}

.goods-item-image {
  position: relative;
  width: 100%;
  height: 370rpx;
  background: #f5f5f5;
  flex-shrink: 0;

  .goods-pic {
    width: 100%;
    height: 100%;
    display: block;
  }
}

.goods-item-info {
  padding: 20rpx;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .goods-name {
    font-size: 30rpx;
    color: #333;
    line-height: 40rpx;
    margin-bottom: 0rpx;
    height: 40rpx;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    flex-shrink: 0;
  }

  .goods-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rpx;
    height: 30rpx;
    flex-shrink: 0;

    .goods-price {
      display: flex;
      align-items: baseline;
      color: #c00000;

      .price-symbol {
        font-size: 24rpx;
        font-weight: 500;
      }
      .price-num {
        font-size: 36rpx;
        font-weight: 700;
      }
    }

    .goods-location {
      display: flex;
      align-items: center;
      
      .location-text {
        font-size: 22rpx;
        color: #1890ff;
        background-color: #f0f9ff;
        padding: 4rpx 10rpx;
      }
    }
  }

  /* üî• ÂÖ≥ÈîÆ‰øÆÊîπÔºöÂèëÂ∏ÉËÄÖ‰ø°ÊÅØË°å */
  .publisher-info {
    display: flex;
    align-items: center;
    justify-content: space-between; // Â∑¶Âè≥ÂàÜÂ∏É
    padding-top: 4rpx;
    height: 40rpx; // Â¢ûÂä†È´òÂ∫¶‰ª•ÂÆπÁ∫≥ÂÜÖÂÆπ
    flex-shrink: 0;

    // üî• Êñ∞Â¢ûÔºöÂ∑¶‰æßÂÆπÂô®ÔºàÂ§¥ÂÉè+ÊòµÁß∞Ôºâ
    .publisher-left {
      display: flex;
      align-items: center;
      flex: 1; // Âç†ÊçÆÂâ©‰ΩôÁ©∫Èó¥
      min-width: 0; // ÂÖÅËÆ∏ÊñáÊú¨Ê∫¢Âá∫ÈöêËóè
    }

    .publisher-avatar {
      width: 40rpx;
      height: 40rpx;
      border-radius: 50%;
      margin-right: 10rpx;
      background: #f5f5f5;
      flex-shrink: 0;
    }

    .publisher-name {
      font-size: 20rpx;
      color: #666;
      max-width: 150rpx; // ÂáèÂ∞èÊúÄÂ§ßÂÆΩÂ∫¶Ôºå‰∏∫Êó∂Èó¥ÁïôÁ©∫Èó¥
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      flex-shrink: 1; // ÂÖÅËÆ∏Êî∂Áº©
    }

    // üî• ‰øÆÊîπÔºöÊó∂Èó¥Ê†∑ÂºèÔºàÈù†Âè≥ÊòæÁ§∫Ôºâ
    .card-time {
      font-size: 20rpx;
      color: #999;
      white-space: nowrap; // Èò≤Ê≠¢Êç¢Ë°å
      flex-shrink: 0; // ‰∏çÂÖÅËÆ∏Êî∂Áº©
      margin-left: 10rpx; // ‰∏éÊòµÁß∞‰øùÊåÅÈó¥Ë∑ù
    }
  }
}
</style>
